@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  border-bottom: #dedede 1px solid;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  text-align: left;
  display: flex;
  justify-content: space-between;
  z-index: 5;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: inline-block;
}

nav ul:nth-child(2) {
  text-align: right;
}

nav li {
  display: inline-block;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 1rem 2rem;
}

nav a:hover {
  color: #ad686a;
  text-decoration: none;
}

.content {
  margin-top: 10rem;
}

.profileimg-container {
  width: 25%;
  overflow: hidden;
  display: inline-block;
}

.profileimg-container img {
  max-width: 100%;
  max-height: 100%;
}

.homepage-text {
  width: 60%;
  display: inline-block;
  background-color: #fff1c9;
  padding: 2rem 4rem;
  border-radius: 5px;
}

.homepage {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.homepage-text h1 {
  display: inline;
  color: #ad686a;
}
.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title img {
  display: inline-block;
  margin-left: 1rem;
}

@media (max-width: 800px) {
  .homepage {
    display: block;
  }
  .profileimg-container {
    display: block;
    width: 75%;
    margin: 5rem auto;
  }
  .homepage-text {
    width: 80%;
    padding: 0.5rem 2rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .title {
    display: block;
  }
  .title h1 {
    margin-bottom: 1rem;
    display: block;
  }
  .title img {
    margin-left: 0;
    margin-right: 1rem;
  }
}

.projects-list-card {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  margin-bottom: 10rem;
}
.projects-list-card h1 {
  display: inline-block;
  color: #ad686a;
}

.projects-list-card img {
  object-fit: contain;
  max-width: 100%;
  max-height: auto;
}
.image-container img:hover {
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}
.projects-list-card a {
  text-decoration: none;
  color: black;
}
.projects-list-card a:hover {
  text-decoration: none;
  color: black;
  text-decoration: underline;
}
.projects-list-card .image-container {
  width: 100%;
  overflow: hidden;
}

.project-preview p {
  text-align: left;
  width: 50%;
  margin-top: 1rem;
  display: block;
  margin-left: 1rem;
}
.project-preview h1 {
  margin-top: 1rem;
  font-weight: 300;
  margin-bottom: 0;
}
.project-title p {
  margin-left: 0;
  margin-top: 0.5rem;
  width: 100%;
}

.project-preview {
  display: flex;
  justify-content: space-between;
}

.more-button {
  display: inline-block;
}

.more-button a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 0.75rem;
  border: 1px #d8d8d8 solid;
  border-radius: 5px;
  margin-top: 1rem;
}

.more-button a:hover {
  text-decoration: underline;
  background-color: #e9e9e9;
}

@media (max-width: 1200px) {
  .projects-list-card {
    width: 70%;
  }
}

@media (max-width: 800px) {
  .projects-list-card {
    width: 100%;
  }
  .project-title {
    display: block;
    width: 100%;
  }
  .project-preview {
    display: block;
  }
  .project-preview p {
    width: 100%;
    margin-left: 0;
  }
  .projects-list-card h1 {
    display: inline-block;
    font-size: 1.75rem;
  }

  .project-title p {
    font-size: 0.9rem;
  }
}

.proj-page-card img {
  max-width: 45%;
  max-height: 100%;
  object-fit: cover;
  overflow: hidden;
  margin-right: 5%;
}

.proj-page-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2.5rem 0;
}

.proj-page-card h1 {
  display: block;
  width: 100%;
}

.proj-page-card-text {
  display: inline-block;
  margin-right: 5%;
  width: 45%;
}

.proj-page-card-text-2 {
  display: inline-block;
}

.proj-page-card-img {
  width: 100%;
  display: block;
  object-fit: cover;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  margin: 2.5rem 0;
}

.proj-page-card-img img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  overflow: hidden;
}

@media (max-width: 800px) {
  .proj-page-card img {
    display: block;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
  }
  .proj-page-card-text {
    display: block;
    width: 100%;
  }
  .proj-page-card {
    display: block;
  }

  .proj-page-card-text-2 p,
  .proj-page-card-text p {
    font-size: 0.9rem;
  }
}

.content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5rem;
}

.proj-page-title h1 {
  display: inline-block;
  width: 50%;
  margin-top: 1rem;
  color: #ab5666;
  font-weight: 300;
  font-size: 3rem;
  margin-right: 1rem;
}

.proj-page-title p {
}

.proj-page-title {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
  /* save for media query 
  align-items: center;*/
}

.proj-page-header-img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}
.proj-page-card-img p {
  margin-bottom: 2rem;
}
.proj-page-side-info {
  display: inline-block;
  width: 50%;
  margin-top: 1.5rem;
}

.proj-page-side-info p {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
@media (max-width: 1200px) {
  .content {
    width: 90%;
  }
}
@media (max-width: 800px) {
  .content {
    width: 90%;
  }
  .projects-list-card h1 {
    display: inline-block;
    font-size: 1.75rem;
  }
  .proj-page-title h1 {
    font-size: 1.75rem;
    width: 100%;
    display: block;
    margin-bottom: 0.1rem;
  }
  .proj-page-title {
    display: block;
  }
  .proj-page-side-info {
    margin-top: 0.5rem;
    width: 100%;
  }
  h1 {
    font-size: 1.5rem;
  }

  .proj-page-side-info p {
    font-size: 0.9rem;
  }
}

